.sub-menu {
    position: fixed;
    top: 40px;
    right: 40px;
    display: none;
    transition: 0.5s;
    opacity: 0.6;
    z-index: 10;
    background: none;
    border: none;
}

.show-menu {
    position: fixed;
    right: 0;
    width: 150px;
    background-color: rgb(255, 255, 255);
    margin-left: 50px;
    border-bottom: 55px;
    z-index: 10;
    list-style-type: none;
    display: none;
    border-radius: 32px 0 0 32px;
    transition: 1s;
    box-shadow: 0px 25px 50px 25px #f7f7ff;

}

.show-menu li a {
    color: #091156;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
}

li {
    margin: 20px 0 20px 50px;
